'use client';

import { useParams } from 'next/navigation';
import Link from 'next/link';
import { useEffect, useState } from 'react';
import { getMeterBySerialNumber, type SmartMeter } from '@/lib/sample-data';
import { Header } from '@/components/Header';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Checkbox } from '@/components/ui/checkbox';
import { Label } from '@/components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Textarea } from '@/components/ui/textarea';
import { Separator } from '@/components/ui/separator';
import { Skeleton } from '@/components/ui/skeleton';
import { User, MapPin, ArrowLeft, Mail, MessageSquare } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

export default function AlertPage() {
  const params = useParams();
  const { toast } = useToast();
  const [meter, setMeter] = useState<SmartMeter | null>(null);
  const [loading, setLoading] = useState(true);
  const [isScheduled, setIsScheduled] = useState(false);
  const [outageMessage, setOutageMessage] = useState('');
  const [tamperingMessage, setTamperingMessage] = useState('');

  useEffect(() => {
    if (typeof params.id === 'string') {
      const meterData = getMeterBySerialNumber(params.id);
      setMeter(meterData || null);
    }
    setLoading(false);
  }, [params.id]);
  
  const generateOutageAlert = () => {
    if (!meter) return;
    const scheduledText = isScheduled 
      ? `This is a scheduled maintenance outage.`
      : `This is an unscheduled power outage.`;
    const message = `[SEMS OUTAGE ALERT]\n\nDear ${meter.name},\nWe are notifying you of a power outage affecting your service at ${meter.address} (Meter: ${meter.serialNumber}).\n\n${scheduledText}\n\nOur team is working to restore power as soon as possible. We apologize for the inconvenience.`;
    setOutageMessage(message);
  };

  const generateTamperingAlert = () => {
    if (!meter) return;
    const message = `[SEMS TAMPERING ALERT]\n\nURGENT: Tampering has been detected on meter ${meter.serialNumber} located at ${meter.address}, registered under ${meter.name}.\n\nService has been disconnected for safety. A technician will be dispatched to investigate. Please contact our support team immediately.`;
    setTamperingMessage(message);
  };

  const sendNotification = (alertType: string, message: string, method: 'SMS' | 'GMail') => {
    if(!message) {
        toast({
            variant: "destructive",
            title: "Error",
            description: "Please generate a message before sending.",
        });
        return;
    }
    toast({
        title: "Notification Sent",
        description: `${alertType} has been sent to ${meter?.name} via ${method}.`,
    });
  }

  if (loading) {
    return (
      <div className="flex min-h-screen w-full flex-col">
        <Header />
        <main className="flex flex-1 justify-center p-4 pt-12 md:p-8"><Skeleton className="h-[500px] w-full max-w-2xl" /></main>
      </div>
    );
  }

  if (!meter) {
    return (
      <div className="flex min-h-screen w-full flex-col">
        <Header />
        <main className="flex flex-1 items-center justify-center"><h1 className="text-2xl font-bold">Meter Not Found</h1></main>
      </div>
    );
  }

  return (
    <div className="flex min-h-screen w-full flex-col">
      <Header />
      <main className="flex flex-1 justify-center bg-muted/40 p-4 pt-12 md:p-8">
        <div className="w-full max-w-2xl space-y-6">
            <Button asChild variant="outline" className="w-fit">
              <Link href="/dashboard">
                <ArrowLeft className="mr-2 h-4 w-4" />
                Back to Dashboard
              </Link>
            </Button>
            <Card>
                <CardHeader>
                    <CardTitle className="font-headline">Meter Details</CardTitle>
                </CardHeader>
                <CardContent className="grid gap-2">
                    <div className="flex items-center gap-3">
                        <User className="h-4 w-4 text-muted-foreground"/>
                        <span className="font-semibold">{meter.name}</span>
                    </div>
                    <div className="flex items-center gap-3">
                        <MapPin className="h-4 w-4 text-muted-foreground"/>
                        <span className="text-sm text-muted-foreground">{meter.address}</span>
                    </div>
                    <p className="text-sm text-muted-foreground">Serial: <span className="font-mono">{meter.serialNumber}</span></p>
                </CardContent>
            </Card>

            <Tabs defaultValue="outage" className="w-full">
                <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="outage">Outage Alert</TabsTrigger>
                    <TabsTrigger value="tampering">Tampering Alert</TabsTrigger>
                </TabsList>
                <TabsContent value="outage">
                    <Card>
                    <CardHeader>
                        <CardTitle className="font-headline">Outage Notification</CardTitle>
                        <CardDescription>Generate and send outage alerts to the customer.</CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="flex items-center space-x-2">
                            <Checkbox id="scheduled" checked={isScheduled} onCheckedChange={(checked) => setIsScheduled(Boolean(checked))} />
                            <Label htmlFor="scheduled">Mark as scheduled outage</Label>
                        </div>
                        <Button onClick={generateOutageAlert}>Generate Alert Message</Button>
                        <Separator />
                        <Label htmlFor="outage-message">Generated Message</Label>
                        <Textarea id="outage-message" placeholder="Generated alert message will appear here..." value={outageMessage} readOnly rows={8} />
                    </CardContent>
                    <CardFooter className="flex justify-end gap-2">
                        <Button variant="outline" onClick={() => sendNotification('Outage Alert', outageMessage, 'SMS')}>
                            <MessageSquare className="mr-2 h-4 w-4" /> Send via SMS
                        </Button>
                        <Button onClick={() => sendNotification('Outage Alert', outageMessage, 'GMail')}>
                            <Mail className="mr-2 h-4 w-4" /> Send via GMail
                        </Button>
                    </CardFooter>
                    </Card>
                </TabsContent>
                <TabsContent value="tampering">
                    <Card>
                    <CardHeader>
                        <CardTitle className="font-headline">Tampering Notification</CardTitle>
                        <CardDescription>Generate and send tampering alerts to the customer and authorities.</CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-4">
                         <Button onClick={generateTamperingAlert}>Generate Alert Message</Button>
                        <Separator />
                        <Label htmlFor="tampering-message">Generated Message</Label>
                        <Textarea id="tampering-message" placeholder="Generated tampering alert will appear here..." value={tamperingMessage} readOnly rows={8} />
                    </CardContent>
                    <CardFooter className="flex justify-end gap-2">
                        <Button variant="outline" onClick={() => sendNotification('Tampering Alert', tamperingMessage, 'SMS')}>
                            <MessageSquare className="mr-2 h-4 w-4" /> Send via SMS
                        </Button>
                        <Button onClick={() => sendNotification('Tampering Alert', tamperingMessage, 'GMail')}>
                            <Mail className="mr-2 h-4 w-4" /> Send via GMail
                        </Button>
                    </CardFooter>
                    </Card>
                </TabsContent>
            </Tabs>
        </div>
      </main>
    </div>
  );
}
