
export interface SmartMeter {
  serialNumber: string;
  name: string;
  address: string;
  time: string;
  power: number;
  tamperingStatus: 'Clear' | 'Detected';
  actuationState: 'Connected' | 'Disconnected';
  status: 'Online' | 'Offline';
  isSenior: boolean;
  isLifeline: boolean;
}

const sampleNames = [
  'Juan dela Cruz', 'Maria Santos', 'Jose Reyes', 'Anna Garcia', 'Peter Lim',
  'Sofia Rodriguez', 'David Chen', 'Isabella Aquino', 'Miguel Tan', 'Chloe Bautista',
  'Andres Bonifacio', 'Gabriela Silang', 'Emilio Aguinaldo', 'Teresa Magbanua', 'Apolinario Mabini',
];

const sampleAddresses = [
  '123 Session Road, Baguio City, Benguet',
  '456 Harrison Road, Baguio City, Benguet',
  '789 Burnham Park, Baguio City, Benguet',
  '101 Military Cut-off Road, Baguio City, Benguet',
  '212 Camp John Hay, Baguio City, Benguet',
  '333 La Trinidad, Benguet',
  '444 Itogon, Benguet',
  '555 Bokod, Benguet',
  '666 Tublay, Benguet',
  '777 Atok, Benguet',
  '888 Kibungan, Benguet',
  '999 Mankayan, Benguet',
  '111 Bakun, Benguet',
  '222 Buguias, Benguet',
  '321 Kabayan, Benguet',
];

// Use a static data template for consistency. The 'time' will be updated dynamically.
const METER_DATA_TEMPLATE: Omit<SmartMeter, 'time'>[] = Array.from({ length: 15 }, (_, i) => {
    const isTampered = i === 2 || i === 10;
    const isOffline = i === 5 || i === 13;
    const isOnline = !isTampered && !isOffline;

    // Static power levels for consistency
    const powerLevels = [1.2, 2.5, 0, 3.1, 1.8, 0, 2.2, 1.5, 3.5, 2.8, 0, 1.9, 2.7, 0, 3.0];
    const power = isOnline ? powerLevels[i] : 0;

    return {
      serialNumber: (10000 + i).toString().padStart(5, '0'),
      name: sampleNames[i % sampleNames.length],
      address: sampleAddresses[i % sampleAddresses.length],
      power: power,
      tamperingStatus: isTampered ? 'Detected' : 'Clear',
      actuationState: isOnline ? 'Connected' : 'Disconnected',
      status: isOnline ? 'Online' : 'Offline',
      isSenior: i % 5 === 0,
      isLifeline: i === 7 || i === 14,
    };
});

const withCurrentTime = (meter: Omit<SmartMeter, 'time'>): SmartMeter => ({
    ...meter,
    time: new Date().toLocaleTimeString('en-US', { hour: 'numeric', minute: '2-digit', second: '2-digit', hour12: true }),
});


export const getMeterData = (): SmartMeter[] => {
    return METER_DATA_TEMPLATE.map(withCurrentTime);
};

export const getMeterBySerialNumber = (serialNumber: string): SmartMeter | undefined => {
  const meterTemplate = METER_DATA_TEMPLATE.find(meter => meter.serialNumber === serialNumber);
  return meterTemplate ? withCurrentTime(meterTemplate) : undefined;
}
